











import Vue from 'vue'
import { MUTATIONS } from '@/store'

export default Vue.extend({
  props: {
    item: Object
  },

  methods: {
    removeItem () {
      this.$store.commit(MUTATIONS.REMOVE_TODOITEM, this.item.id)
    },

    completeTodoItem () {
      this.$store.commit(MUTATIONS.TOGGLE_COMPLETE, this.item.id)
    }
  }
})



